import Icon from "@/assets/icon.png";
import downIcon from "@/assets/downIcon.png";
import upIcon from "@/assets/upIcon.png";
import one from "@/assets/one.png";
import two from "@/assets/two.png";
import three from "@/assets/three.png";
import noConten from "@/assets/zwsj.png";
import loading from "@/assets/loading.gif";
export default {
  data() {
    return {
      noConten: noConten,
      loading: loading,
      isShow: true,
      one: one,
      two: two,
      three: three,
      Icon: Icon,
      upIcon: upIcon,
      downIcon: downIcon,
      form: { user_name: "",uid:""},
      list: [],
      activeClass: ""
    };
  },
  mounted() {
    this.getAbacus();
    this.getRanking();
  },
  methods: {
    getFH() {
      this.$router.push("/FHrecord");
    },
    getSP() {
      this.$router.push("/SPrecord");
    },
    getAbacus() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/abacus/abacus-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.isShow = false;
            that.form = response.data;
            that.form.user_name =
              response.data.user_name.substring(0, 1) + "**";
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getRanking() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/abacus/ranking",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
          var reg = /^[\u4e00-\u9fa5]+$/;
          response.data.forEach(val => {
            if (reg.test(val.user_name)) {
              val.user_name = val.user_name.substring(0, 1) + "**";
            } else {
              val.user_name = val.user_name.substring(0, 3) + "****";
            }
            //isActive
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};



